:root {
  font-size: 16px;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #282c34;
  line-height: 1.6; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 20px;
  padding: 0; }

p {
  margin-top: 0;
  margin-bottom: 20px;
  padding: 0;
  font-weight: normal; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

figure {
  display: inline-block; }

img {
  display: inline-block;
  vertical-align: middle; }

.App {
  position: relative; }
  .App_Container {
    background: #FFFFFF;
    padding: 40px;
    margin: 0; }
  .App_Logo {
    width: 100px;
    height: auto; }
  @media (prefers-reduced-motion: no-preference) {
    .App_Logo {
      animation: App-logo-spin infinite 20s linear; } }
  .App_Header {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    color: white;
    padding: 15px 40px; }
    .App_Header nav ul {
      display: flex;
      flex-direction: row;
      margin: 0;
      padding: 0; }
      .App_Header nav ul li {
        list-style-type: none; }
        .App_Header nav ul li a {
          display: inline-block;
          color: #61dafb;
          border-width: 3px;
          border-style: solid;
          border-color: #61dafb;
          border-radius: 10px;
          font-size: 1.1rem;
          margin: 0 10px;
          padding: 10px;
          text-decoration: none;
          transition: all .4s linear; }
          .App_Header nav ul li a:hover {
            border-color: #FFF;
            color: #FFF;
            transition: all .4s linear; }
  .App .swapi-wrapper {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    width: 100%;
    padding-top: 20px; }
    .App .swapi-wrapper aside {
      border-width: 3px;
      border-style: solid;
      border-color: #000;
      border-radius: 10px;
      padding: 15px;
      width: 100%;
      max-width: 425px;
      margin-bottom: 20px; }
      .App .swapi-wrapper aside figure {
        margin: 0 auto; }
      .App .swapi-wrapper aside p {
        font-size: 0.9em; }
  .App .post-wrapper img {
    width: auto;
    max-width: 100%; }
  .App footer {
    padding: 40px;
    color: #61dafb;
    text-align: center; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@media screen and (max-width: 759px) {
  .App_Header {
    flex-flow: column wrap;
    justify-content: center; }
    .App_Header figure {
      margin: 0 auto 20px auto; }
    .App_Header nav ul {
      flex-direction: column;
      align-items: center; }
      .App_Header nav ul li {
        list-style-type: none;
        margin-bottom: 15px; }
        .App_Header nav ul li:last-child {
          margin-bottom: 0 !important; }
        .App_Header nav ul li a {
          flex-direction: row;
          justify-content: space-between; } }
